const SelectWithLabel = function(props) {
  return (
    <FormRow>
      <LabelRow>
        <Label small color={theme.palette.text.secondary}>
          {props.label}
        </Label>
      </LabelRow>
      <Select
        name="interactor_type"
        value={props.selected}
        onChange={e => props.handleChange(e)}
      >
        {props.items.map(i => (
          <option key={i.type} value={i.type}>
            {i.label}
          </option>
        ))}
      </Select>
    </FormRow>
  );
};

export { SelectWithLabel };
