import React from "react";
import styled from "styled-components";
import { theme } from "../../Variables";

const BaseButton = styled.button`
  position: relative;
  width: ${props => (props.size ? props.size : "30px")};
  height: ${props => (props.size ? props.size : "30px")};
  box-sizing: border-box;
  // font-size: 0.875rem;
  // font-weight: 700;
  // text-transform: uppercase;
  // letter-spacing: 0.05rem;
  color: ${props => (props.color ? "#fff" : theme.palette.text.primary)};
  transition: color 0.5s ease;
  background-color: ${props =>
    props.color
      ? theme.palette[props.color].main
      : theme.palette.text.secondary};
  transition: background-color 0.5s ease;
  padding: ${props => (props.padding ? props.padding : "0")};
  border: none;
  border-radius: 50%;
  cursor: pointer;
  outline: none;

  &:hover {
    background-color: ${theme.palette.primary.dark};
  }
`;

export const IconButton = function(props) {
  let Btn = BaseButton;
  if (props.variant === "outlined" || props.variant === "text") {
    Btn = styled(Btn)`
      color: ${props =>
        props.color
          ? theme.palette[props.color].main
          : theme.palette.text.secondary};
      border: 1px solid
        ${props =>
          props.color
            ? theme.palette[props.color].main
            : theme.palette.text.secondary};
      background-color: transparent;
      transition: background-color 150ms ${theme.transitions.easing.easeInOut};

      &:hover {
        background-color: ${props =>
          props.color
            ? theme.palette[props.color].darkTransparent
            : theme.palette.grey["100"]};
      }
    `;

    if (props.variant === "text") {
      Btn = styled(Btn)`
        border: none;
      `;
    }
  } else if (props.variant === "") {
  }

  return <Btn {...props}>{props.children}</Btn>;
};
