import React from "react";
import "./VideoTree.css";
import PropTypes from "prop-types";

import { buildStructure, flattenStructure } from "../../../../utils/tree.js";

import NodeExpansion from "../NodeEditor";
import ContentOverlay from "../../../../Components/ContentOverlay";
import Styles from "../../../../Components/Material/Styles.module.css";
import { IconButton } from "../../../../Components/Material/Buttons/IconButton";
import { MdMoreVert, MdModeEdit, MdDelete, MdAdd } from "react-icons/md";
import {
  Menu,
  MenuItem,
  MenuItemText,
  MenuItemIcon
} from "../../../../Components/Material/Menu";

class VideoTree extends React.Component {
  constructor(props) {
    super(props);

    this.scrollRef = null;

    this.state = {
      expandNodeMenu: 0,
      expandNode: 0, // node.id default 0
      nodes: [],
      random: 0 // just used as key for player iframe
    };

    this.toggleExpandNodeMenu = this.toggleExpandNodeMenu.bind(this);
    this.toggleExpandNode = this.toggleExpandNode.bind(this);

    this.updateNode = this.updateNode.bind(this);
    this.availableParents = this.availableParents.bind(this);
    this.createNode = this.createNode.bind(this);
    this.deleteNode = this.deleteNode.bind(this);
  }

  toggleExpandNodeMenu(e, nodeId) {
    e.stopPropagation();
    const id = nodeId === this.state.expandNodeMenu ? 0 : nodeId;
    this.setState({ expandNodeMenu: id });
  }

  toggleExpandNode(e, nodeId) {
    setTimeout(_ => this.setState({ expandNodeMenu: 0 }), 100);
    e.stopPropagation();
    this.setState({ expandNode: nodeId });
  }

  availableParents(node, tree) {
    const flattenedNode = flattenStructure(node);
    const nodeIds = flattenedNode.map(n => n.id);
    return tree.filter(n => {
      return nodeIds.indexOf(n.id) === -1 && n.id !== node.parent_id;
    });
  }

  createNode(e, nodeId) {
    setTimeout(_ => this.setState({ expandNodeMenu: 0 }), 100);
    e.stopPropagation();
    // e.nativeEvent.stopImmediatePropagation();
    this.props.createNode(nodeId);
  }

  deleteNode(e, nodeId) {
    setTimeout(_ => this.setState({ expandNodeMenu: 0 }), 100);
    e.stopPropagation();
    // e.nativeEvent.stopImmediatePropagation();
    this.props.deleteNode(nodeId);
  }

  updateNode(node) {
    setTimeout(_ => this.setState({ expandNodeMenu: 0, expandNode: 0 }), 100);
    this.props.updateNode(node);
  }

  buildTree(nodes, nodesKey, parentIsActive = true) {
    console.log(parentIsActive);
    return (
      <ul className="tree-unordered-list">
        {nodes.map(node => {
          let nodeIsActive = parentIsActive;
          if (node.parent_id > 0) {
            console.log(node.parent_id);
            const parent = this.props.nodes.filter(n => {
              return n.id === node.parent_id;
            });

            if (nodeIsActive) {
              nodeIsActive = false;
              if (typeof parent[0] !== "undefined") {
                if (parent[0].interactions && parent[0].interactions.fork) {
                  nodeIsActive = parent[0].interactions.fork.enabled === true;
                }
              }
            }
          }
          return (
            <li className="tree-list-item" key={"li_" + node.id}>
              <span
                style={{ position: "relative", paddingRight: "5px" }}
                // onClick={(e) => this.toggleExpandNode(e, node.id)}
                className={`node-title node-content ${
                  nodeIsActive ? "" : "node-inactive"
                } ${Styles.elevation1}`}
              >
                <div
                  style={{ display: "flex", justifyContent: "space-between" }}
                >
                  <div
                    style={{
                      height: "30px",
                      lineHeight: "30px",
                      marginRight: "10px"
                    }}
                  >
                    {node.title}
                  </div>
                  <div>
                    <IconButton
                      variant="text"
                      onClick={e => this.toggleExpandNodeMenu(e, node.id)}
                    >
                      <MdMoreVert size="1rem" />
                    </IconButton>
                    {this.state.expandNodeMenu === node.id ? (
                      <Menu>
                        <MenuItem onClick={e => this.createNode(e, node.id)}>
                          <MenuItemText>ADD NODE</MenuItemText>
                          <MenuItemIcon>
                            <MdAdd size="1rem" />
                          </MenuItemIcon>
                        </MenuItem>
                        <MenuItem
                          onClick={e => this.toggleExpandNode(e, node.id)}
                        >
                          <MenuItemText>EDIT</MenuItemText>
                          <MenuItemIcon>
                            <MdModeEdit size="1rem" />
                          </MenuItemIcon>
                        </MenuItem>
                        <MenuItem onClick={e => this.deleteNode(e, node.id)}>
                          <MenuItemText>DELETE</MenuItemText>
                          <MenuItemIcon>
                            <MdDelete size="1rem" />
                          </MenuItemIcon>
                        </MenuItem>
                      </Menu>
                    ) : (
                      ""
                    )}
                  </div>
                  {/* <button onClick={(e) => this.createNode(e, node.id)}>+</button>
                                    <button onClick={(e) => this.deleteNode(e, node.id)}>-</button> */}
                </div>

                {this.state.expandNode === node.id ? (
                  <ContentOverlay>
                    <div>
                      <iframe
                        title="Player Preview"
                        key={this.state.random}
                        // TODO check autoplay param in player and disable autoplay
                        src={`https://localhost:3000?projectId=${
                          this.props.projectId}&nodeId=${node.id}&autoplay=false`}
                        allow="autoplay"
                      />
                    </div>
                    <NodeExpansion
                      node={node}
                      availableParents={this.availableParents(
                        node,
                        this.props.nodes
                      )}
                      updateNode={this.updateNode}
                      closeNodeExpansion={this.toggleExpandNode}
                    />
                  </ContentOverlay>
                ) : (
                  ""
                )}
              </span>
              {typeof node[nodesKey] !== "undefined" &&
              node[nodesKey].length > 0
                ? this.buildTree(node[nodesKey], nodesKey, nodeIsActive)
                : ""}
            </li>
          );
        })}
      </ul>
    );
  }

  componentDidMount() {
    // center tree container content
    this.scrollRef.scrollLeft =
      (this.scrollRef.querySelector(".tree").offsetWidth -
        this.scrollRef.offsetWidth) /
      2;

    const nodes = [buildStructure(this.props.nodes)];
    this.setState({ nodes, random: this.state.random + 1 });
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    if (prevProps.nodes !== this.props.nodes) {
      const nodes = [buildStructure(this.props.nodes)];
      this.setState({ nodes });
    }
  }

  render() {
    // e.g. your child nodes might have a different name so you can pass the key as a prop.
    const nodesKey = this.props.nodesKey ? this.props.nodesKey : "nodes";
    return (
      <div
        className="tree-scroller"
        style={{ height: "100%", overflowX: "scroll", overflowY: "scroll" }}
        ref={ref => (this.scrollRef = ref)}
      >
        <div className="tree" style={{ width: "5000px", height: "100%" }}>
          {this.buildTree(this.state.nodes, nodesKey)}
        </div>
      </div>
    );
  }
}

VideoTree.propTypes = {
  projectId: PropTypes.number.isRequired,
  nodesKey: PropTypes.string,
  nodes: PropTypes.array.isRequired,
  createNode: PropTypes.func.isRequired,
  updateNode: PropTypes.func.isRequired
};

export default VideoTree;
