import React from "react";
import BreadCrumbs from "../../Components/Material/BreadCrumbs";
import { Redirect } from "react-router-dom";
import Ajax from "../../utils/ajax";

class Create extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      id: 0,
      title: "",
      redirectToEdit: false
    };

    this.handleChange = this.handleChange.bind(this);
    this.createProject = this.createProject.bind(this);
  }

  handleChange(e) {
    let state = { ...this.state };
    // only have a title at this time
    state.title = e.target.value;
    this.setState(state);
  }

  createProject() {
    if (process.env.NODE_ENV === "development") {
      let state = { ...this.state };
      state.id = 123;
      state.redirectToEdit = true;
      this.setState(state);
      return;
    }

    Ajax.post("/projects", { title: this.state.title })
      .then(res => {
        console.log(res);
        if (typeof res.project.id !== "undefined") {
          let state = { ...this.state };
          state.id = res.project.id;
          state.redirectToEdit = true;
          this.setState(state);
        }
      })
      .catch(err => console.error(err));
  }

  render() {
    const links = [
      { name: "projects", path: "/projects" },
      { name: "create", path: "/projects/create" }
    ];
    return (
      <>
        <BreadCrumbs links={links} />
        <div>
          <input
            onChange={this.handleChange}
            type="text"
            value={this.state.title}
          />
          <button onClick={this.createProject}>Create</button>
        </div>
        {this.state.redirectToEdit ? (
          <Redirect to={`/projects/${this.state.id}`} />
        ) : (
          ""
        )}
      </>
    );
  }
}

export default Create;
