import React from "react";
import {
  Card,
  CardContent,
  CardActions
} from "../../../Components/Material/Card";
import { TextInputWithLabel } from "../../../Components/Material/Form/Inputs/Text";
import { Button } from "../../../Components/Material/Buttons";
import { SelectWithLabel } from "../../../Components/Material/Form/Inputs/Select";

class NodeExpansion extends React.Component {
  interactorTypes = [
    { type: "disabled", label: "Disabled" },
    { type: "on_start", label: "On Start" },
    { type: "on_end", label: "On End" }
    // { type: 'custom_time', label: 'After x seconds' },
  ];

  constructor(props) {
    super(props);

    this.state = {
      node: {
        id: "",
        title: "",
        url: "",
        parent_id: false,
        interactions: { fork: { enabled: false, type: "custom_time" } },
        nodes: []
      }
    };

    this.handleChange = this.handleChange.bind(this);
    this.updateNode = this.updateNode.bind(this);
    this.toggleInteractor = this.toggleInteractor.bind(this);
    this.changeParent = this.changeParent.bind(this);
  }

  handleChange(e) {
    e.preventDefault();
    const value = e.target.value;
    // console.log(e.target.name);
    let node = { ...this.state.node };
    if (e.target.name === "interactor_type") {
      node.interactions.fork.enabled = value !== "disabled";
      node.interactions.fork.type = value;
    } else {
      node[e.target.name] = value;
    }

    this.setState({ node });
  }

  toggleInteractor() {
    let node = { ...this.state.node };
    node.interactions.fork.enabled = !node.interactions.fork.enabled;
    this.setState({ node });
  }

  changeParent(id) {
    let node = { ...this.state.node };
    node.parent_id = id;
    this.setState({ node });
  }

  updateNode() {
    this.props.updateNode(this.state.node);
  }

  componentDidMount() {
    const node = { ...this.state.node, ...this.props.node };
    this.setState({ node });
  }

  render() {
    const { node } = this.state;
    // console.log('node: ', node);
    return (
      <Card width="300px">
        <CardContent>
          <TextInputWithLabel
            label="Title"
            name="title"
            width="75%"
            value={node.title}
            onChange={this.handleChange}
          />
          <TextInputWithLabel
            label="Video Url"
            name="url"
            value={node.url}
            spellcheck="false"
            onChange={this.handleChange}
          />
          <SelectWithLabel
            label="Show Interaction"
            selected={
              node.interactions.fork.enabled
                ? node.interactions.fork.type
                : "disabled"
            }
            items={this.interactorTypes}
            handleChange={this.handleChange}
            marginBottom="0"
          />
        </CardContent>
        <CardActions>
          {/* TODO when cancel, revert state changes to prevstate */}
          <Button
            variant="text"
            size="small"
            onClick={e => this.props.closeNodeExpansion(e, 0)}
          >
            cancel
          </Button>
          <Button
            variant="text"
            color="primary"
            size="small"
            onClick={this.updateNode}
          >
            save
          </Button>
        </CardActions>
      </Card>
    );
  }
}

export default NodeExpansion;
