import React from "react";
import styled from "styled-components";
import { theme } from "../../Variables";
import FormRow from "../FormRow";
import { Label, LabelRow } from "../FormLabel";

export const Input = styled.input`
  width: ${props => (props.width ? props.width : "100%")};
  color: ${theme.palette.text.primary};
  padding: 6px 5px 6px 1px;
  border: none;
  border-bottom: 1px solid ${theme.palette.text.secondary};
  outline: none;
`;

export class TextInputWithLabel extends React.Component {
  defaultColor = theme.palette.text.secondary;
  defaultBorderStyle = `1px solid ${theme.palette.text.secondary}`;
  activeColor = theme.palette.primary.main;

  constructor(props) {
    super(props);

    this.state = {
      labelColor: this.defaultColor,
      borderStyle: this.defaultBorderStyle,
      showLabel: false
    };

    this.handleFocus = this.handleFocus.bind(this);
    this.handleBlur = this.handleBlur.bind(this);
  }

  handleFocus() {
    let state = { ...this.state };
    state.labelColor = this.activeColor;
    state.borderStyle = `2px solid ${this.activeColor}`;
    state.showLabel = true;
    this.setState(state);
  }

  handleBlur() {
    let state = { ...this.state };
    state.labelColor = this.defaultColor;
    state.borderStyle = this.defaultBorderStyle;
    state.showLabel = false;
    this.setState(state);
  }

  render() {
    // const props = this.props;
    const { name, value, label } = this.props;
    const placeholder = this.state.showLabel ? "" : label;
    return (
      <FormRow marginBottom={this.props.marginBottom}>
        {value || this.state.showLabel ? (
          <LabelRow>
            <Label small color={this.state.labelColor}>
              {label}
            </Label>
          </LabelRow>
        ) : (
          ""
        )}
        <Input
          type="text"
          name={name}
          value={value}
          placeholder={placeholder}
          spellcheck={() =>
            this.props.spellcheck ? this.props.spellcheck : "true"
          }
          width={this.props.width}
          style={{ borderBottom: this.state.borderStyle }}
          onChange={this.props.onChange}
          onFocus={this.handleFocus}
          onBlur={this.handleBlur}
        />
      </FormRow>
    );
  }
}
